const RepoLink = (props) => {
    return (
        <a 
          className="repos-linksWrap-link"
          href={props.html_url}>{props.name}
        </a>
    )
}

export const makeRepoList = (repos) => {
    let result = [];
    let length = repos.length > 3 ? 3 : repos.length;

    for (let i = 0; i < length; i++) {
      result.push(
        <RepoLink 
          key={repos[i].id} 
          html_url={repos[i].html_url} 
          name={repos[i].name}/>
      )
    }
    
    return result;
}