import React, { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
import {makeRepoList} from './Repos';


const User = ({user}) => {
  const [repos, setRepos] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch(`${user.repos_url}?per_page=3`)
      .then(res => {
        return res.json()
      })
      .then(data => {
        setRepos(data);
        setLoading(false)
        console.log(data);
      })
  }, [])

  makeRepoList(repos);
  
  return (
    <li 
      className="users-wrapper__item"
    >
      <div className="users-wrapper__item-info">
        <img 
              className="users-wrapper__item-info-img" 
              src={user.avatar_url} 
              alt="user_img"
        />
        <span className="users-wrapper__item-info-type">
          Type: {user.type} â€”
        </span>

        {/* <a 
          className="users-wrapper__item-info-link" 
          href={`/${user.login}`} target="_blank"
        >
          {user.login}
        </a> */}
        <Link to={`/${user.login}`} />
      </div>

      <div className="repos">
        <div className="text">Repositories:</div>
        <div className="repos-linksWrap">
          {
              loading ? null :
              repos.length === 0 ? null :
              makeRepoList(repos).map(item => item)
          }
        </div>
      </div>
    </li>
  )
}

export default User;