import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';

import {makeRepoList} from './Repos';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';

const backIco = <FontAwesomeIcon icon={faArrowLeft} />

const API_URL = 'https://api.github.com/users/';

const OrganizationsList = ({orgz}) => {
    return (
        <li className="userPage-organizations-ul__item">
            <img 
                className="userPage-organizations-ul__item-img"
                src={orgz.avatar_url}></img>
            <a 
                className="userPage-organizations-ul__item-link"
                href={`https://github.com/${orgz.login}`}
                target="_blank"
            >
                 {orgz.login}
            </a>
        </li>
    )
}

const Organizations = ({user}) => {
    const [orgzs, setOrgzs] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(async () => {
        await fetch(`${user.organizations_url}`)
        .then(res => {
          return res.json()
        })
        .then(data => {
          setOrgzs(data);
          setLoading(false);
        })
    }, []);

    return (
        <>
        {
            loading ? <>Loading Organizations...</> :
                orgzs.length === 0 ? <>No organizations!</> :
                    <ul className="userPage-organizations-ul">
                        {
                            orgzs.map(orgz => <OrganizationsList key={orgz.id} orgz={orgz}/>)
                        }
                    </ul>
        }
        </>
    )
}

const UserPageInfo = ({user}) => {
    const repos = makeRepoList(user)

    let history = useHistory();

    const handleClick = () => {
      history.push("/");
    }

    return (
        <>
            <div onClick={handleClick} className="userPage-back">{backIco}</div>
            <div className="userPage-imgWrap">
                <img   
                    className="userPage-imgWrap__img"
                    src={user.avatar_url} 
                    alt="user_img"
                />
            </div>
            <div className="userPage-name">
                <a 
                    className="userPage-name__link" 
                    href={user.html_url}
                    target="_blank"
                >
                    {user.login}
                </a>
            </div>
            <div className="userPage-type">
                Type: {user.type}
            </div>
            <div className="userPage-reposWrap">
                Repositories: {repos.length > 0 ? repos 
                : <>Empty</>
                }
            </div>
            
            <div className="userPage-organizations">
                Organizations:
                <Organizations user={user}/>
            </div>
        </>
    )
}

const UserPage = () => {
    const [user,setUser] = useState(null);
    const [loading, setLoading] = useState(true)
    const {username} = useParams();

    useEffect(() => {
        loadData();
    }, []);

    const loadData = async () => {
        try {
            const response = await fetch(`${API_URL}${username}`);
            const data = await response.json();
            setUser(data);
            setLoading(false)
        } catch(err) {
            console.error(err)
        }
    }

    return (
      <div className="userPage userPage-outer">
          <div className="userPage-inner container">
                {loading ? <>Loading...</> : 
                    !user.hasOwnProperty('login') ? 
                        <>User Not Found...</> : 
                        <UserPageInfo user={user}/>}
          </div>
      </div>
    );
}

export default UserPage;